<template>
  <div class="wrap">
    <div class="top" style="width: 100%;height: 4rem; background-color: rgb(49,144,232);">
      <span style="font-size: 3rem;line-height:4rem;padding-left:1rem;color: white;font-weight: bolder;"
            @click="goback"> <</span>
      <span style="font-size: 3rem;line-height:4rem;padding-left:9rem;color: white;font-weight: bolder;">下载</span>
    </div>
    <img src="../assets/饿了.png" height="180" width="180" style="margin: 2rem 8rem"/>
    <p style="text-align:center;font-size: 2rem;">下载饿了么APP</p>
    <van-button type="primary" size="large" @click="download">下载</van-button>
    <div v-show="local" class="box animated bounceInDown"
         style="width: 70%;height: 20rem;position: absolute;background-color: white; margin:-16rem 5rem;border-radius: 10%;overflow: hidden">
      <img src="../assets/叹号.png" height="128" width="128" style="margin: 1rem 6rem"/>
      <p style="text-align: center;font-weight: bolder"> IOS用户请到Appstore下载</p>
      <van-button type="primary" size="large" @click="jump">下载</van-button>
    </div>













  </div>
</template>

<script>

  export default {

    name: "download",
    data() {
      return {
        local: false,
      }
    },
    methods: {
      goback() {
        this.$router.go(-1);
      },
      download() {
        this.local = true
      },
      jump(){
        this.local=false
      }
    }

  }
</script>

<style scoped>



</style>



