<template>
  <div class="wrap">
    <div class="top">
      <van-icon name="arrow-left" class="left" @click="goback"/>
      <span>我的</span>
    </div>
    <div class="center" @click="login">
      <div class="img">
        <van-icon name="manager" class="imgN"/>
      </div>
      <span class="denglu">{{this.pN}}</span>
      <span class="a"><van-icon name="graphic"/>暂无绑定手机号</span>
    </div>
    <div class="cen">
      <div class="yue" @click="yue">
        <span class="ling">0.00</span>
        <span class="yuan">元</span>
        <p class="Wyue">我的余额</p>
      </div>
      <div class="youhui" @click="youhui">
        <span class="ling">3</span>
        <span class="yuan">个</span>
        <p class="Wyue">我的优惠</p>
      </div>
      <div class="jifen" @click="jifen">
        <span class="ling">0</span>
        <span class="yuan">分</span>
        <p class="Wyue">我的积分</p>
      </div>
    </div>
    <div class="Na"></div>
    <p style="border-bottom: 0.01rem solid gray;margin-top: 2rem">
      <router-link :to="{path:'dingdan'}"><img src="../assets/订单.png" height="25" width="25"/>我的订单</router-link>
    </p>
    <p style="border-bottom: 0.01rem solid gray;margin-top: 2rem">
      <router-link :to="{path:'jifenshangcheng'}"><img src="../assets/积分商城.png" height="25" width="25"/>积分商城</router-link>
    </p>

    <p style="border-bottom: 0.01rem solid gray;margin-top: 2rem">
      <router-link :to="{path:'huiyuan'}"><img src="../assets/会员卡.png" height="25" width="25"/>饿了么会员卡</router-link>
    </p>

    <p style="border-bottom: 0.01rem solid gray;margin-top: 2rem">
      <router-link :to="{path:'fuwu'}"><img src="../assets/服务中心-01.png" height="25" width="25"/>服务中心</router-link>
    </p>

    <p style="border-bottom: 0.01rem solid gray;margin-top: 2rem">
      <router-link :to="{path:'download'}"><img src="../assets/饿了么.png" height="25" width="25"/>下载饿了么APP</router-link>
    </p>
  </div>
</template>

<script>
  import Vue from 'vue'
  export default {
    name: "Mine",
    data() {
      return {
        pN: '',
      }
    },
    methods: {
      goback() {
        this.$router.go(-1);
      },
      login() {
        this.$router.push({path: '/login'})
      },
      yue(){
        this.$router.push({path: '/yue'})
      },
      youhui(){
        this.$router.push({path: '/youhui'})
      },
      jifen(){
        this.$router.push({path: '/jifen'})

      }


    },
    mounted() {
      //获取数据
      this.pN = JSON.parse(localStorage.getItem('allinfor')).username
      //存储数据
      // localStorage.setItem("pN",JSON.stringify(allinfor))
    }
  }
</script>

<style scoped>
  .top {
    width: 100%;
    background-color: rgb(49, 144, 232);
    height: 4rem;
    border-bottom: 0.01rem solid white;
  }

  .left {
    font-size: 2rem;
    color: white;
    line-height: 4rem;
  }

  .yuan {
    position: absolute;
    margin-left: 6rem;
    margin-top: 2.5rem;
  }

  .Na {
    width: 100%;
    height: 1rem;
    background-color: rgb(235, 235, 235);
  }

  .Wyue {
    position: absolute;
    margin-top: 6rem;
    margin-left: 2rem;
    font-size: 1.5rem;
  }

  .top span {
    font-size: 2rem;
    color: white;
    line-height: 4rem;
    margin-left: 12rem;
  }

  .cen {
    width: 100%;
    height: 8rem;

  }

  .ling {
    position: absolute;
    margin-top: 2rem;
    margin-left: 2rem;
    color: orange;
    font-size: 2.5rem;
    font-weight: bolder;
  }

  .yue {
    border-right: 0.01rem solid gray;
    width: 33%;
    height: 8rem;
    position: absolute;

  }

  .youhui {
    border-right: 0.01rem solid gray;
    width: 33%;
    height: 8rem;
    position: absolute;
    left: 33.5%;
  }

  .jifen {
    border-right: 0.01rem solid gray;
    position: absolute;
    left: 67%;
    width: 33%;
    height: 8rem;

  }

  .img {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    overflow: hidden;
    margin-left: 2rem;
    background-color: rgb(200, 200, 200);
    margin-top: 2rem;
  }

  .imgN {
    font-size: 5rem;
  }

  .center {
    background-color: rgb(49, 144, 232);
    margin-top: -2rem;
    color: white;
  }

  .denglu {
    position: absolute;
    font-size: 1.5rem;
    left: 8rem;
    top: 4rem;
    font-weight: bolder;
  }

  .a {
    position: absolute;
    margin-left: 8rem;
    margin-top: -2rem;
  }
</style>



