<template>
  <div class="wrap">
    <div class="top">
      <b class="b" @click="goback"> < </b>
      <b class="merchants">商家详情</b>
    </div>
    <div class="Na"></div>
    <div class="shuxing">活动与属性</div>
    <span class="jian">{{jian}}</span>
    <span class="man">{{man}}(APP专享)</span>
    <span class="bao">{{bao}}</span>
    <p class="jiaru">{{jiaru}}(APP专享)</p>
    <span class="zhun">{{zhun}}</span>
    <span class="zhunshi">{{zhunshi}}(APP专享)</span>
    <span class="piao">{{piao}}</span>
    <p class="zhichi">{{zhichi}}(APP专享)</p>
    <div class="Na"></div>
    <router-link :to="{path:'/Business'}">
      <div class="business">
        <span class="gongshi">食品监督安全公示</span>
        <span class="qiye">企业认证详情 ></span>
      </div>
    </router-link>
    <div class="jiandu">
      <span class="jieguo">监督检查结果:</span><span :style="{color:color1}">{{pingpan}}</span>
      <p class="jiancha">检查日期:</p>

    </div>
    <div class="Na"></div>
    <div class="shangjia"><p>商家信息</p></div>
    <div class="k"><p> {{kname}}</p></div>
    <div class="k"><p> 地址：{{dizhi}}</p></div>
    <div class="k"><p> 营业时间：{{shijian}}</p></div>
    <van-button style="width: 100%;height: 4rem;;" @click="showPopup">
      <span class="yingye">营业执照</span>
    </van-button>
    <van-popup v-model="show"><img :src="'http://elm.cangdu.org/img/'+ imgSrc"></van-popup>

    <van-button style="width: 100%;height: 4rem;;" @click="showPopup">
      <span class="xuke">餐饮服务许可证</span>
    </van-button>
    <van-popup v-model="show"><img :src="'http://elm.cangdu.org/img/'+ imgSrc"></van-popup>
  </div>


</template>

<script>
  import Vue from 'vue'

  export default {
    name: "Merchants",
    data() {
      return {
        rest: [],
        jian: '',
        man: '',
        bao: '',
        jiaru: '',
        zhun: '',
        zhunshi: '',
        piao: '',
        zhichi: '',
        pingfen: '',
        pingpan: '',
        color1: "red",
        kname: '',
        dizhi: '',
        shijian: '',
        imgSrc: '',
        show: false
      }
    },
    mounted() {
      this.jian = this.$route.query.jian;
      this.man = this.$route.query.man;
      this.bao = this.$route.query.bao;
      this.jiaru = this.$route.query.jiaru;
      this.zhun = this.$route.query.zhun;
      this.zhunshi = this.$route.query.zhunshi;
      this.piao = this.$route.query.piao;
      this.zhichi = this.$route.query.zhichi;
      this.pingfen = this.$route.query.pingfen;
      this.kname = this.$route.query.kname;
      this.dizhi = this.$route.query.dizhi;
      this.shijian = this.$route.query.shijian;
      this.imgSrc = this.$route.query.imgSrc;

      if (this.pingfen == 5) {
        this.pingpan = '良好'
        this.color1 = "green";
      } else {
        this.pingpan = '差',
          this.color1 = "red";
      }


      Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude=31.22967&longitude=121.4762', null).then((result) => {
        console.log(result.data)
        this.rest = result.data
      }).catch((error) => {
        console.log(error)
      })
    },
    methods: {
      goback() {
        this.$router.go(-1)
      },
      showPopup() {
        this.show = true;
      }
    }
  }
</script>

<style scoped>
  .top {
    width: 100%;
    height: 4rem;
    background-color: rgb(49, 144, 232);
  }

  b {
    line-height: 5rem;
    font-size: 2rem;
    color: rgb(255, 255, 255);
  }

  .b {
    font-size: 3rem;
    margin-left: 1rem;
  }

  .merchants {
    margin: 0 10rem;
  }

  .Na {
    width: 100%;
    height: 1rem;
    background-color: rgb(235, 235, 235);
  }

  .shuxing {
    width: 100%;
    height: 4rem;
    border-bottom: 0.01rem solid rgb(241, 241, 241);
    font-size: 2rem;
    line-height: 4rem;
    color: rgb(51, 51, 51);
  }

  .jian {
    background-color: rgb(240, 115, 115);
    margin-left: 2rem;
    line-height: 3rem;
    border-radius: 0.2rem;
    color: white;
  }

  .man {
    margin-left: 1rem;
    line-height: 3rem;
  }

  .bao, .piao {
    position: absolute;
    left: 2rem;
    margin-top: 3rem;
    background-color: rgb(153, 153, 153);
    color: white;
    border-radius: 0.2rem;

  }

  .jiaru {
    margin-left: 4.5rem;
  }

  .zhun {
    background-color: rgb(87, 169, 255);
    border-radius: 0.2rem;
    position: absolute;
    left: 2rem;
    color: white;
  }

  .zhunshi {
    margin-left: 4.5rem;
  }

  .zhichi {
    margin-left: 4.5rem;
    margin-top: 1.5rem;
  }

  .business {
    width: 100%;
    height: 4rem;
    border-bottom: 0.01rem solid rgb(241, 241, 241);
    line-height: 4rem;
  }

  .gongshi {
    font-size: 2rem;
    color: black;
    margin-left: 1rem;
  }

  .qiye {
    font-size: 1.5rem;
    color: gray;
    margin-left: 6rem;

  }

  .jiandu {
    font-size: 1.5rem;
  }

  .jieguo, .jiancha {
    margin-left: 5rem;
    top: 1rem;
    font-weight: bolder;
  }

  .shangjia {
    width: 100%;
    height: 3rem;
    border-bottom: 0.01rem solid rgb(241, 241, 241);
  }

  .shangjia p {
    margin-left: 1rem;
    font-size: 2rem;
    font-weight: bolder;
  }

  .k {
    width: 100%;
    height: 4rem;
    border-bottom: 0.01rem solid rgb(241, 241, 241);
    font-size: 1.5rem;
  }

  .k p {
    margin-left: 1rem;
    font-weight: bolder;
    line-height: 2rem;
  }

  .yingye,.xuke {
    top: -0.1rem;
    left: 1rem;
    position: absolute;
    font-size: 2rem;
  }
</style>



