<template>
  <div class="swiper">
    <van-row
      style="font-size:2rem ; font-weight:900; line-height: 3rem; color: white; height: 3rem; background-color: rgb(49,144,232);">
      <van-col span="5" offset="1">
        <router-link :to="{path:'/adress'}" style="color:white;">
          <van-icon name="search" style="color:white;"/>
        </router-link>

      </van-col>
      <van-col span="6" offset="4">
        <router-link :to="{path:'/Map'}" style="color: white;">地址</router-link>
      </van-col>
      <router-link :to="{path:'/login'}" style="color: white;">登录|注册</router-link>

    </van-row>

    <swiper :options="swiperOption" ref="mySwiper" class="v_swiper">
      <!-- slides -->
      <swiper-slide v-for="smallArr in oneimg">
        <!--<div class="one">-->
        <ul>
          <li v-for="item in smallArr" style="width: 15%;">
            <router-link :to="{path:'allRest'}">
              <img :src="'https://fuss10.elemecdn.com/'+ item.image_url">
              <br>
              {{item.title}}
            </router-link>
          </li>
        </ul>
        <!--</div>-->
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <div class="center"></div>
    <div class="bott">
      <img :src=url alt="" class="urlimg">
      <span>附近商家</span>
    </div>
    <div class="allRestaurant">
      <div v-for="(item,index) in rest" class="rest-a">
        <div @click="choose(index)">
          <div class="right">
            <img :src="'http://elm.cangdu.org/img/'+ item.image_path">
            <!--店名-->
            <span class="pinpai">品牌</span>
            <span class="mingzi">{{item.name}}</span>
            <p class="baozhun">保准票</p>
            <!--星星★-->
            <van-rate :size="3"
                      color="gold"
                      void-icon="star"
                      void-color="wheat" v-model="item.rating" class="vantRa"/>
            <!--评分-->
            <span class="pingfen">{{item.rating}}</span>
            <!--月销售量-->
            <span class="yueshou">月售{{item.recent_order_num}}单</span>
            <!--蜂鸟专送-->
            <span class="fengniao">{{item.delivery_mode.text}}</span>
            <span class="zhunshida">准时达</span>
            <!--起送价-->       <!--配送费-->
            <span class="peisong">￥{{item.float_minimum_order_amount}}起送/配送费￥{{item.float_delivery_fee}}</span>
            <!--10公里-->
            <span class="gongli">10公里/</span>
            <!--40分钟-->
            <span class="fenzhong">40分钟</span>
          </div>
        </div>
      </div>
    </div>
    <van-tabbar v-model="active">
      <router-link :to="{path:'chooseFood'}">
        <van-tabbar-item icon="home-o" class="a">外卖</van-tabbar-item>
      </router-link>
      <router-link :to="{path:'search'}">
        <van-tabbar-item icon="search" class="a">搜索</van-tabbar-item>
      </router-link>
      <router-link :to="{path:'dingdan'}">
        <van-tabbar-item icon="todo-list" class="a">订单</van-tabbar-item>
      </router-link>
      <router-link :to="{path:'Mine'}">
        <van-tabbar-item icon="friends-o" class="a">我的</van-tabbar-item>
      </router-link>
    </van-tabbar>

  </div>

</template>

<script>
  import Vue from 'vue'

  export default {
    name: "chooseFood",
    data() {
      return {
        active: 0,
        oneimg: [],
        rest: [],
        icon_name:'',
        swiperOption: {
          direction: 'horizontal', // 水平切换选项
          loop: true, // 循环模式选项
          autoplay: 1000,//可选选项，自动滑动
          paginationClickable: true,
          // observer: true
          // autoplayDisableOnInteraction: false,
          pagination: '.swiper-pagination',
        },
        url: "https://timgsa.baidu.com/timg?image&quality=8" +
          "0&size=b9999_10000&sec=1564726979607&di=f66715a933c0c42cf335f0622" +
          "2b7b249&imgtype=0&src=http%3A%2F%2Fbpic.588ku.com%2Felement_origin_min" +
          "_pic%2F23%2F04%2F20%2F16571aca678fc95.jpg"

      }
    },
    mounted() {
      Vue.axios.get('https://elm.cangdu.org/v2/index_entry', null).then((result) => {
        console.log(result.data)
        var oneArr = result.data.slice(0, 8);
        var twoArr = result.data.slice(8);
        console.log(oneArr);
        console.log(result.data[0])
        this.oneimg = [oneArr, twoArr];

      }).catch((error) => {
        console.log(error);
      })
      Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude=31.22967&longitude=121.4762', null).then((result) => {
        console.log(result.data)
        this.rest = result.data
      }).catch((error) => {
        // console.log(error)
      })
    },
    methods: {
      choose(v) {
        console.log(this.rest[v].activities[0].icon_name);
        this.$router.push({
          path: '/searchEat',
          query: {
            imgSrc: this.rest[v].image_path,
            kname: this.rest[v].name,
            peisongfei: this.rest[v].piecewise_agent_fee.tips,
            gonggao: this.rest[v].promotion_info,
            jian: this.rest[v].activities[0].icon_name,
            man: this.rest[v].activities[0].description,
            //保
            bao: this.rest[v].supports[0].icon_name,
            //已加入
            jiaru: this.rest[v].supports[0].description,
            //准
            zhun: this.rest[v].supports[1].icon_name,
            //准时必达，超时秒陪
            zhunshi: this.rest[v].supports[1].description,
            //票
            piao: this.rest[v].supports[2].icon_name,
            //支持开发票
            zhichi: this.rest[v].supports[2].description,
            //评分
            pingfen: this.rest[v].rating,
            dizhi: this.rest[v].address,
            shijian: this.rest[v].opening_hours[0],
            id:this.rest[v].id,


          }
        })

      }

    }
  }
</script>

<style scoped>
  .v_swiper {
    width: 100%;
    height: 40%;
    border-bottom: 1px solid gray;
  }

  ul li {
    list-style-type: none;
    display: inline-block;
    margin-left: 8%;
    text-align: center;
  }

  li img {
    width: 4rem;
    height: 3rem;
  }

  .center {
    width: 100%;
    height: 2rem;
    background-color: whitesmoke;
  }

  .bott {
    width: 100%;
    height: 5rem;
    font-size: 2rem;
    line-height: 4rem;
    /*margin-left: 2rem;*/
  }

  .urlimg {
    width: 2rem;
    height: 2rem;
  }

  .rest-a {
    width: 100%;
    height: 10rem;
  }

  .rest-a img {
    width: 6rem;
    height: 6rem;
    margin-left: 1rem;
  }

  .baozhun {
    position: absolute;
    margin-top: -6rem;
    margin-left: 26rem;
    color: gray;
  }
.a{
  margin-left: 4.5rem;
}
  .pinpai {
    background-color: gold;
    font-size: 1rem;
    position: absolute;
    margin-left: 0.2rem;
    margin-top: 0.5rem;
    color: black;
  }

  .mingzi {
    position: absolute;
    font-size: 1.8rem;
    margin-top: 0.2rem;
    margin-left: 3rem;
    font-weight: bolder;
    color: black;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .vantRa {
    position: absolute;
    margin-left: 7.5rem;
    margin-top: -4rem
  }

  .pingfen {
    position: absolute;
    margin-top: 2rem;
    margin-left: 7rem;
    color: red;
  }

  .yueshou {
    position: absolute;
    margin-top: 2rem;
    margin-left: 9rem;
    color: black;
  }

  .fengniao {
    position: absolute;
    margin-top: 2rem;
    margin-left: 16rem;
    background-color: lightskyblue;
    font-size: 1rem;
    color: white;
  }

  .zhunshida {
    position: absolute;
    margin-top: 2rem;
    margin-left: 21rem;
    border: 1px solid cyan;
    font-size: 1rem;
  }

  .peisong {
    position: absolute;
    margin-left: 0.5rem;
    margin-top: 4rem;
    color: gray;

  }

  .gongli {
    position: absolute;
    margin-top: 4rem;
    margin-left: 15rem;
    color: gray;

  }

  .fenzhong {
    position: absolute;
    margin-top: 4rem;
    margin-left: 20rem;
  }

</style>











