<template>
  <div class="wrap">
    <div class="top" style="width: 100%;height: 16rem; background-color: rgb(49,144,232);">
      <span style="font-size: 3rem;line-height:4rem;padding-left:1rem;color: white;font-weight: bolder;"
            @click="goback"> <</span>
      <span style="font-size: 3rem;line-height:4rem;padding-left:9rem;color: white;font-weight: bolder;">我的积分</span>

      <div style="border-top: 0.05rem solid white"></div>
      <div style="overflow: hidden; background-color: whitesmoke; width: 95%;height: 70%;margin: 0.5rem auto;border-radius: 0.5rem">

        <p style="padding-left: 2rem">当前积分</p>
        <span style="padding-left: 3rem ;font-size: 4rem;font-weight: bolder">0.00</span><span>积分</span>
        <van-button @click="btn" type="primary" style="background-color: rgb(254,109,71);border: 0" size="large">积分兑换商品</van-button>
      </div>
    </div>
    <div v-show="local" class="box animated bounceInUp"
         style="width: 70%;height: 20rem;position: absolute;background-color: white; margin:-16rem 5rem;border-radius: 10%;overflow: hidden">
      <img src="../assets/叹号.png" height="128" width="128" style="margin: 1rem 6rem"/>
      <p style="text-align: center;font-weight: bolder"> 快去下单赚取大量积分吧</p>
      <van-button type="primary" size="large" @click="jump">go die</van-button>
    </div>


  </div>
</template>

<script>
  export default {
    name: "jifen",
    methods: {
      goback() {
        this.$router.go(-1)
      },
      btn(){
        this.local=true
      },
      jump(){
        this.local=false
      }
    },
    data(){
return{
  local: false,
}
    }
  }
</script>

<style scoped>

</style>



