<template>
  <div class="wrap">
    <div class="top" style="width: 100%;height: 4rem; background-color: rgb(49,144,232);">
      <span style="font-size: 3rem;line-height:4rem;padding-left:1rem;color: white;font-weight: bolder;"
            @click="goback"> <</span>
      <span style="font-size: 3rem;line-height:4rem;padding-left:9rem;color: white;font-weight: bolder;">兑换会员</span>
    </div>
    <p style="text-align: center;font-weight: bolder">成功兑换后将关联到当前帐号：{{pN}}</p>
    <input v-model="value1" oninput="if(value.length>10)value=value.slice(0,10)" style="width: 100%;height: 4rem;border-right: 0;border-left: 0;border-top: 0;" type="number"
           placeholder="请输入10位卡号">
    <input v-model="value2" oninput="if(value.length>6)value=value.slice(0,6)"  style="width: 100%;height: 4rem;border-right: 0;border-left: 0;border-top: 0;" type="number"
           placeholder="请输入6位卡密">

    <van-button :style="{backgroundColor:hhh}" :disabled="flag" type="primary" size="large">兑换</van-button>
    <!--<van-button @click="" type="primary" size="large">兑换</van-button>-->

    <h2 style="text-align: center">——温馨提示——</h2>
    <p style="text-align: center">韩国政府决定，自9月起，把日本从现有的29个出口管理优惠国名单中移除，列入新增的第三类出口国。日本外务省干部表示，“我们将在确认韩方采取本次措施的理由，及具体内容等详细情况之后，再决定如何应对。”

      韩国《中央日报》表示，韩国把日本剔除出口白色清单，能否给日本带来冲击还是未知数。由于钢铁、金属、尖端科技材料等战略性物资，韩国对日本出口的比率微乎其微，韩国严管对日出口，反会给韩国企业带来更大打击。</p>


  </div>

</template>

<script>
  export default {
    name: "duihuan",
    data() {
      return {
        value1:'',
        value2: '',
        pN:'',
        right:true,
        hhh:'grey',
      }
    },
    computed:{
      flag(){
        if(!(this.value1.length==10)||!(this.value2.length==6)){
          this.hhh='grey'
        }else{
          this.hhh='green'
        }
        // return !(this.value1.length==10)||!(this.value2.length==6)
      }
    },
    methods: {
      goback() {
        this.$router.go(-1)
      },



    },
    mounted() {
      this.pN = JSON.parse(localStorage.getItem('allinfor')).username

    }
  }
</script>

<style scoped>

</style>



