<template>
    <div>
      <van-row
        style="font-size:2rem ; font-weight:900; line-height: 3rem; color: white; height: 3rem; background-color: rgb(49,144,232);">
        <van-col span="5" offset="1">
          <van-icon name="arrow-left" @click="btn"/>
        </van-col>
        <van-col span="10" offset="4">搜索</van-col>
      </van-row>
      <input type="text" placeholder="请输入商家或美食名称" ><van-button type="primary" size="small">搜索</van-button>
    </div>
</template>

<script>
  import Vue from 'vue'
  export default {
        name: "adress",
methods:{
  btn(){
    this.$router.go(-1)
  }
}
    }
</script>

<style scoped>
input{
  width: 60%;
  height: 3rem;
  margin-left: 2rem;
  border-radius: 1rem;
}
</style>



