<template>
  <div>
    <p @click="goback">返回</p>


    <input type="text" placeholder="请输入商家或美食" class="inp">
    <button class="sub">搜索</button>
  </div>
</template>

<script>
    export default {
        name: "search",
      methods:{
        goback(){
          this.$router.go(-1);
        },
      }
    }
</script>

<style scoped>
.inp{
  width: 80%;
  height: 2rem;
  border-radius:15%;
  margin-top: 20%;
}
  .sub{
    background-color: dodgerblue;
    width: 15%;
    height: 2rem;
    margin-top: 20%;

  }





</style>



