<template>
  <div class="wrap">
    <van-row
      style="font-size:2rem ; font-weight:900; line-height: 3rem; color: white; height: 3rem; background-color: rgb(49,144,232);">
      <van-col span="5" offset="1">
        <van-icon name="arrow-left" @click="btn"/>
      </van-col>
      <van-col span="10" offset="4">密码登录</van-col>
    </van-row>
    <van-cell-group>
      <van-field v-model="value1" placeholder="账号"/>
      <van-field v-model="value2" placeholder="旧密码"/>

      <van-field v-model="value3" placeholder="新密码" class="k"/>
      <van-field v-model="value4" placeholder="确认新密码"/>
      <van-field v-model="value5" type="password" placeholder="验证码"/>
      <img :src=imgM alt="" class="imgM">
      <div class="change" @click="change" style="font-size:0.5rem;"><p>看不清</p>
        <a>换一张</a></div>

    </van-cell-group>
    <van-button type="primary" size="large">登录</van-button>

  </div>
</template>
<script>
  import Vue from 'vue'

  export default {
    name: "again",
    data() {
      return {
        checked: true,
        imgM: '',
        value1: '',
        value2: '',
        value3: '',
      };
    },
    mounted() {
      Vue.axios.post('https://elm.cangdu.org/v1/captchas', null).then((result) => {
        // console.log(result.data.code)
        this.imgM = result.data.code
      })
    },
    methods: {
      change() {
        Vue.axios.post('https://elm.cangdu.org/v1/captchas', null).then((result) => {
          // console.log(result.data.code)
          this.imgM = result.data.code
        })
      },
      btn(){
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped>

  .imgM {
    position: absolute;
    right: 7rem;
    top: 15rem;
  }
  .change {
    position: absolute;
    right: 2rem;
    top: 15rem;
  }
</style>



