<template>
  <div class="wrap">
    <div class="top">
      <router-link :to="{path:'/chooseFood'}">
        <img :src="'http://elm.cangdu.org/img/'+ imgSrc" class="imgA">
        <span class="kname">{{kname}}</span>
        <span class="p">{{peisong}}</span>
        <span class="p">{{fenzhong}}</span>
        <span class="p">{{peisongfei}}</span>
        <p class="gonggao">公告:{{gonggao}}</p>
        <span class="jian">{{jian}}</span>
        <span class="man">{{man}}(APP专享)</span>
      </router-link>
      <div>
        <router-link :to="{path: '/jump',query:{'kname':kname,'jian':jian,'man':man,'gonggao':gonggao}}"><span
          class="huodong">一个活动》</span></router-link>
      </div>
      <div>
        <router-link
          :to="{path: '/Merchants',query:{'jian':jian,'man':man,'bao':bao,'jiaru':jiaru,'zhun':zhun,'zhunshi':zhunshi,'piao':piao,'zhichi':zhichi,'pingfen':pingfen,'kname':kname,'dizhi':dizhi,'shijian':shijian,'imgSrc':imgSrc}}">
          <b> > </b></router-link>
      </div>
    </div>

    <van-tabs v-model="active">
      <van-tab title="商品">
        <van-sidebar v-for="(index,item) in allname" v-model="activeKey">
          <van-sidebar-item @click="imgSrc1(index)" :title="index.name"/>
        </van-sidebar>
        <div class="k"><span>{{this.key}}</span></div>
        <div class="cen">
          <img :src="'http://elm.cangdu.org/img/'+ imgSrc" class="imgA">
          <span class="tn">{{this.Name}}</span>
          <span class="tp">{{this.pp}}</span>
          <span class="ta">{{this.aa}}</span>
          <p class="tt"> {{this.tips}}</p>
          <p class="ti"> {{this.image_text}}</p>
          <img src="../assets/add.png" height="20" width="20" id="icon" @click="ck()"/>
        </div>

      </van-tab>
      <van-tab title="评价">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item title="4"/>
          <van-sidebar-item title="5"/>
          <van-sidebar-item title="6"/>
        </van-sidebar>
      </van-tab>
    </van-tabs>
    <div id="ball"></div>
  </div>
</template>
<!--<script ></script>-->
<script>
  // src="http://libs.baidu.com/jquery/2.0.0/jquery.min.js"
  import Vue from 'vue'
  var $ball = document.getElementById('ball');
  export default {
    name: "searchEat",
    data() {
      return {
        activeKey: 0,
        active: 2,
        oneimg: [],
        allname: [],
        rest: [],
        seed: [],
        imgSrc: '',
        kname: '',
        peisong: '商家配送',
        fenzhong: '/分钟送达',
        peisongfei: '',
        gonggao: '',
        jian: '',
        man: '',
        bao: '',
        jiaru: '',
        zhun: '',
        zhunshi: '',
        piao: '',
        zhichi: '',
        pingfen: '',
        dizhi: '',
        shijian: '',
        id: '',
        key: '',
        Name: '',
        pp: '',
        aa: '',
        image_path: '',
        tips: '',
        image_text: '',
      }
    },
    methods: {
      ck(evt) {
        this.$ball.top = evt.pageY + 'px';
       this. $ball.left = evt.pageX + 'px';
        this. $ball.transition = 'left 0s, top 0s';
        setTimeout(() => {
          this.  $ball.top = window.innerHeight + 'px';
          this.$ball.left = '0px';
          this.$ball.transition = 'left 1s linear, top 1s ease-in';
        }, 20)
      },
      imgSrc1(index) {
        if (index.name == "热销榜") {
          this.seed = this.allname;

        }
        else {
          this.seed = this.allname.filter((pro) => {
            return pro.name === index.name;

          })
        }
        console.log(this.seed);
        this.key = this.seed[0].description
        this.Name = this.seed[0].foods[0].name
        this.pp = this.seed[0].name
        this.aa = this.seed[0].foods[0].description
        this.tips = this.seed[0].foods[0].tips
        this.image_text = this.seed[0].foods[0].activity.image_text
        this.image_path = this.seed[0].foods[0].image_path
        console.log(this.seed)
        console.log(this.key)

      },


    },
    mounted() {
      this.imgSrc = this.$route.query.imgSrc;
      console.log(this.imgSrc);
      this.kname = this.$route.query.kname;
      this.peisongfei = this.$route.query.peisongfei;
      this.gonggao = this.$route.query.gonggao;
      this.jian = this.$route.query.jian;
      this.man = this.$route.query.man;
      this.bao = this.$route.query.bao;
      this.jiaru = this.$route.query.jiaru;
      this.zhun = this.$route.query.zhun;
      this.zhunshi = this.$route.query.zhunshi;
      this.piao = this.$route.query.piao;
      this.zhichi = this.$route.query.zhichi;
      this.pingfen = this.$route.query.pingfen;
      this.dizhi = this.$route.query.dizhi;
      this.shijian = this.$route.query.shijian;
      this.id = this.$route.query.id;
      Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude=31.22967&longitude=121.4762', null).then((result) => {
        console.log(result.data)
        this.rest = result.data
      }).catch((error) => {
        // console.log(error)
      })
      Vue.axios.get('https://elm.cangdu.org/shopping/v2/menu', {params: {restaurant_id: this.id}}).then((res) => {
        console.log(res.data)
        this.allname = res.data;
        this.imgSrc1({name: '热销榜'});
      }).catch((error) => {
        // console.log(error)
      })
    }
  }
</script>

<style scoped>
  .wrap {
    width: 100%;
    height: 100%;
  }

  .k {
    position: absolute;
    top: 4rem;
    left: 7rem;
    color: rgb(166, 166, 166);
    background-color: rgb(245, 245, 245);
    width: 30rem;
    height: 4.6rem;
  }

  .k span {
    position: absolute;
    left: 7rem;
    top: 1.5rem;
  }

  .cen {
    position: absolute;
    width: 28rem;
    height: 10rem;
    left: 7rem;
    top: 8.6rem;

  }

  .top {
    width: 100%;
    height: 10rem;
    background-color: gray;
  }

  .imgA {
    height: 6rem;
    width: 6rem;
    margin-top: 1rem;
    margin-left: 1rem;
  }

  .kname {
    color: whitesmoke;
    font-weight: bolder;
    position: absolute;
    font-size: 2rem;
    margin-top: 0.6rem;

  }

  .p {
    font-size: 1.5rem;
    color: whitesmoke;
    /*position: absolute;*/
    margin-top: 0.5rem;
  }

  .gonggao {
    font-size: 1.4rem;
    color: whitesmoke;
    position: absolute;
    margin-left: 7.3rem;
    margin-top: -2rem;
    font-weight: bolder;
  }

  .tt {
    position: absolute;
    margin-left: 7rem;
    margin-top: -2rem;

  }

  .jian {
    background-color: lightpink;
    font-size: 1.5rem;
    color: whitesmoke;
    font-weight: bold;
    position: absolute;
    margin-left: -24rem;
    margin-top: 8rem;
  }

  #icon {
    position: absolute;
    margin-left: 10rem;
    margin-top: 7rem;
  }

  .man {
    position: absolute;
    font-weight: bold;
    color: whitesmoke;
    margin-top: 8rem;
    margin-left: -22rem;
  }

  .ti {
    position: absolute;
    border: 0.01rem solid pink;
    border-radius: 20%;
    margin-left: 7.5rem;
    font-size: 0.5rem;
  }

  .huodong {
    position: absolute;
    color: whitesmoke;
    margin-left: 25rem;
    margin-top: 1rem;
  }

  b {
    font-size: 2rem;
    position: absolute;
    left: 28rem;
    color: whitesmoke;
    top: 2rem;
  }

  .tn {
    position: absolute;
    font-weight: bolder;
    font-size: 2rem;
    margin-top: 1rem;
  }

  .ta {
    position: absolute;
    margin-top: 3rem;
    /*margin-left: -1rem;*/
  }

  .tp {
    position: absolute;
    margin-top: -4rem;
    font-weight: bolder;
    font-size: 2rem;
    margin-left: -6rem;

  }

  #ball {
    width: 12px;
    height: 12px;
    background: #5EA345;
    border-radius: 50%;
    position: fixed;
    transition: left 1s linear, top 1s ease-in;
  }
</style>



