<template>
  <div class="wrap">

    <div class="top">
      <b class="b" @click="goback"> < </b>
      <b class="merchants">商超便利</b>
    </div>

    <van-dropdown-menu>
      <van-dropdown-item v-model="value" :options="option"/>
      <van-dropdown-item title="筛选" ref="item">
        <van-switch-cell v-model="switch1" title="包邮"/>
        <van-switch-cell v-model="switch2" title="团购"/>
        <van-button block type="info" @click="onConfirm">确认</van-button>
      </van-dropdown-item>
    </van-dropdown-menu>
  </div>


</template>

<script>
  export default {
    name: "allRest",
    data() {
      return {
        value: 0,
        switch1: false,
        switch2: false,
        option: [
          {text: '排序', value: 0},
          {text: '距离最近', value: 1},
          {text: '销量最高', value: 2},
          {text: '起送价最低', value: 3},
          {text: '配送速度最快', value: 4},
          {text: '评分最高', value: 5},
        ]
      }
    },
    methods: {
      goback() {
        this.$router.go(-1)
      },
      onConfirm() {
        this.$refs.item.toggle();
      },
    }
  }
</script>

<style scoped>
  .top {
    width: 100%;
    height: 4rem;
    background-color: rgb(49, 144, 232);
  }

  b {
    line-height: 4rem;
    font-size: 2rem;
    color: rgb(255, 255, 255);
  }

  .b {
    font-size: 3rem;
    margin-left: 1rem;
  }

  .merchants {
    margin: 0 10rem;
  }
</style>



