<template>
  <div class="wrap">
    <div class="top" style="width: 100%;height: 4rem; background-color: rgb(49,144,232);">
      <span style="font-size: 3rem;line-height:4rem;padding-left:1rem;color: white;font-weight: bolder;"
            @click="goback"> <</span>
      <span style="font-size: 3rem;line-height:4rem;padding-left:9rem;color: white;font-weight: bolder;">兑换红包</span>
    </div>
    <input type="number" v-model="value1" style="width: 100%;height: 4rem;border-right: 0;border-left: 0;border-top: 0;"
           placeholder="请输入兑换码">
    <input type="number" v-model="value2" oninput="if(value.length>4)value=value.slice(0,4)"
           style="width: 60%;height: 4rem;border-left: 0;border-top: 0;" placeholder="请输入验证码">
    <img :src="code">
    <div style="margin-left:85%;margin-top: -4rem;font-size: 0.8rem;" @click="keys">
      <p>看不清</p>
      <p>换一张</p>
    </div>

    <van-button :style="{backgroundColor:hhh}" :disabled="flag" type="primary" size="large">兑换</van-button>

  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    name: "duihuanhongbao",
    data() {
      return {
        value1: '',
        value2: '',
        hhh: 'grey',
        code: '',
      }
    },
    methods: {

      goback() {
        this.$router.go(-1)
      },
      keys(){
        Vue.axios.post('https://elm.cangdu.org/v1/captchas', {
          exchange_code: this.value1,
          captcha_code: this.value2,
        }).then((res) => {
          this.code = res.data.code
        }).catch((error) => {
          console.log(error)
        })
      }
    },
    computed: {
      flag() {
        if (!(this.value1.length != "") || !(this.value2.length == 4)) {
          this.hhh = 'grey'
        } else {
          this.hhh = 'green'
        }
      }
    },
    mounted(){
    this. keys()
    }
  }
</script>

<style scoped>

</style>



