<template>
  <div class="jump">
    <h2>{{kname}}</h2>
    <p>优惠信息</p>
    <span class="jian">{{jian}}</span>
    <span >{{man}}（App专享)</span>
    <p>商家公告</p>
    <span class="gonggao" >{{gonggao}}</span>
    <div @click="goback"  class="goback">X</div>
  </div>

</template>

<script>
  import Vue from 'vue'
  export default {
    name: "jump",
    data() {
      return {
        rest: [],
        kname: '',
        jian: '',
        man: '',
        gonggao: '',
      }

    },
    mounted() {
      this.kname = this.$route.query.kname,
        this.jian = this.$route.query.jian,
        this.man = this.$route.query.man,
        this.gonggao = this.$route.query.gonggao
      console.log(this.man)
      Vue.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude=31.22967&longitude=121.4762', null).then((result) => {
        console.log(result.data)
        this.rest = result.data
      }).catch((error) => {
        console.log(error)
      })
    },
    methods:{
      goback(){
        this.$router.go(-1) //go（-1）回到上一级
        // this.$router.push({path:'/chooseFood'})回到指定页面
      }
    }

  }
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  .jump {
    background-color: black;
    width: 100%;
    height: 100%;
    color: white;

  }
h2{
  text-align:  center ;
  line-height: 8rem;
}
  p{
    border: 1px solid wheat;
    width: 6rem;
    height: 2rem;
    border-radius: 20%;
    text-align: center;
    margin: 3rem auto;
  }
  .jian{
    background-color: palevioletred;
    border-radius: 20%;
    margin-left: 5rem
  }
  .gonggao{
    margin-left: 5rem;
  }
.goback{
  border: 1px solid wheat;
  width: 5rem;
  height: 5rem;
  line-height: 5rem;
  border-radius: 50%;
  text-align: center;
  font-size: 3rem;
  margin: 6rem auto;
}
</style>



