<template>
  <div class="wrap">
    <div class="top" style="width: 100%;height: 4rem; background-color: rgb(49,144,232);">
      <span style="font-size: 3rem;line-height:4rem;padding-left:1rem;color: white;font-weight: bolder;"
            @click="goback"> <</span>
      <span style="font-size: 3rem;line-height:4rem;padding-left:9rem;color: white;font-weight: bolder;">服务中心</span>
    </div>
    <div style="width: 100%;border-bottom: 0.01rem solid grey;">

      <div style="width: 50%;height: 8rem;border-right: 0.1rem solid grey">
        <img src="../assets/客服 (1).png" height="48" width="48" style="margin:1rem 6rem"/>
        <p style="margin-left: 6rem">在线客服</p>
      </div>
      <div style="width: 50%;height: 8rem;position: absolute;margin-left: 50%;margin-top: -8rem"><img
        src="../assets/电话.png" height="48" width="48" style="margin:1rem 6rem"/>
        <p style="margin-left: 6rem">服务电话</p>
      </div>
    </div>
    <div
      style="width: 100%;height: 6rem;padding-left: 2rem;font-size: 2rem;border-bottom:0.005rem solid grey;line-height: 6rem;">
      热门问题
    </div>
    <div style="padding-left: 2rem;line-height: 2rem">
      <div v-for="(per,key,index) in wordArr" :key="index">
        <p v-if="(index+1) %2 == 0&&index<25&&index!=21&&index!=19" class="dl"><span @click="addreff(per,index,key)">{{per}}》 <hr></span>
        </p>
        <p v-if="index%2 == 0&&index>25" class="dl"><span @click="addreff(per,index)">{{per}}》 <hr></span></p>

      </div>

    </div>



  </div>

</template>

<script>
  import Vue from 'vue'

  export default {
    name: "fuwu",
    methods: {
      goback() {
        this.$router.go(-1)
      }
    },
    data() {
      return {
        wordArr: '',
      }
    },
    mounted() {
      Vue.axios.get('https://elm.cangdu.org/v3/profile/explain', null).then((result) => {
        console.log(result.data)
        this.wordArr = result.data
      }).catch((error) => {
        // console.log(error);
      })

    }
  }
</script>

<style scoped>

</style>



