<template>
  <div class="wrap">
    <div class="top" style="width: 100%;height: 4rem; background-color: rgb(49,144,232);">
      <span style="font-size: 3rem;line-height:4rem;padding-left:1rem;color: white;font-weight: bolder;"
            @click="goback"> <</span>
      <span style="font-size: 3rem;line-height:4rem;padding-left:9rem;color: white;font-weight: bolder;">在线支付</span>
    </div>

    <div style="width: 100%;height:12rem;">
      <van-count-down :time="time" style="line-height: 12rem;font-size: 3rem;text-align: center"/>

    </div>
    <div
      style="width: 100%;height: 5rem;background-color: rgb(241,241,241);font-size: 2rem;padding-left: 2rem;line-height:5rem ">
      <span>选择支付方式</span></div>
    <van-radio-group v-model="radio">
      <img src="../assets/支付宝.png" height="64" width="64"/>
      <van-radio name="1" style="padding-left: 25rem;margin-top: -1rem"></van-radio>
      <div style="border-top: 0.1rem solid darkgrey"></div>
      <img src="../assets/微信 支付-2.png" height="64" width="64"/>
      <van-radio name="2" style="padding-left: 25rem;margin-top: -2rem"></van-radio>
    </van-radio-group>
    <div id="bottom" v-show="local" class="box animated bounceInRight">
      <div
        style="z-index:3;position:absolute;width: 80%;height: 20rem;background-color: navajowhite;margin: -5rem 3rem;border-radius: 10%;">
        <img src="../assets/叹号.png" height="128" width="128" style="margin: 1rem 7rem"/></div>
      <p style="z-index:4;padding-left: 4rem;position: absolute;font-size: 1.5rem;padding-top: 7rem">
        当前无法支付，请打开官方App进行付款</p>
      <button
        style="width: 20rem;height: 4rem;background-color: orange;position: absolute;z-index: 4;border-radius: 10%;margin-top: 10rem;margin-left: 5rem" @click="jump">
        确认
      </button>
    </div>
    <button
      style="background-color: greenyellow;margin-top:4rem;width: 100%;height: 3rem;text-align: center;line-height: 3rem"
      @click="queren">确认支付
    </button>


  </div>
</template>

<script>
  export default {
    name: "zhifu",
    data() {
      return {
        time: 15 * 60 * 1000,
        radio: '',
        local: false

      };
    },
    methods: {
      goback() {
        this.$router.go(-1)
      },
      queren() {
       this.local=true

      },
      jump(){
        this.$router.push({path: '/chooseFood'})
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>

</style>



