import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import  searchEat from "../components/searchEat"
import  chooseFood from "../components/chooseFood"
import  jump from "../components/jump"
import  Merchants from "../components/Merchants"
import  Business from "../components/Business"
import  allRest from "../components/allRest"
import  login from "../components/login"
import  again from "../components/again"
import  adress from "../components/adress"
import  Map from "../components/Map"
import  dingdan from "../components/dingdan"
import  search from "../components/search"
import  Mine from "../components/Mine"
import  jifenshangcheng from "../components/jifenshangcheng"
import  fuwu from "../components/fuwu"
import  huiyuan from "../components/huiyuan"
import  download from "../components/download"
import  tequan from "../components/tequan"
import  zhifu from "../components/zhifu"
import  duihuan from "../components/duihuan"
import  jilu from "../components/jilu"
import  yue from "../components/yue"
import  youhui from "../components/youhui"
import  jifen from "../components/jifen"
import  tuijianyoujiang from "../components/tuijianyoujiang"
import  duihuanhongbao from "../components/duihuanhongbao"

export default new Router({
  routes: [
    {path:'/',component:Map},
    {path:'/searchEat',component:searchEat},
    {path:'/chooseFood',component:chooseFood},
    {path:'/jump',component:jump},
    {path:'/Merchants',component:Merchants},
    {path:'/Business',component:Business},
    {path:'/allRest',component:allRest},
    {path:'/login',component:login},
    {path:'/again',component:again},
    {path:'/adress',component:adress},
    {path:'/Map',component:Map},
    {path:'/dingdan',component:dingdan},
    {path:'/search',component:search},
    {path:'/Mine',component:Mine},
    {path:'/jifenshangcheng',component:jifenshangcheng},
    {path:'/fuwu',component:fuwu},
    {path:'/huiyuan',component:huiyuan},
    {path:'/download',component:download},
    {path:'/tequan',component:tequan},
    {path:'/zhifu',component:zhifu},
    {path:'/duihuan',component:duihuan},
    {path:'/jilu',component:jilu},
    {path:'/yue',component:yue},
    {path:'/youhui',component:youhui},
    {path:'/jifen',component:jifen},
    {path:'/duihuanhongbao',component:duihuanhongbao},
    {path:'/tuijianyoujiang',component:tuijianyoujiang},


  ]
})



